.cards-container {
  margin: 50px 0;
}

.card {
  margin: 0 15px !important;
}

.infected {
  border-bottom: 12px solid rgba(0, 0, 255, 0.5);
}

.recovered {
  border-bottom: 12px solid rgba(0, 255, 0, 0.5);
}

.deaths {
  border-bottom: 12px solid rgba(255, 0, 0, 0.5);
}

@media (max-width: 770px) {
    .card {
        margin: 10px 0 !important;
    }
  }